body {
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue", Helvetica, sans-serif;
    background-image: url(img/BCG_L.jpg);
}

h1 {
    font-size: 40px;
    font-weight: lighter;
    color: white;
    margin: 0;
}

h3 {
    font-weight: lighter;
}

button {
    border: none;
    background-color: transparent;
    color: white;
    outline: none;
    font-size: 30px;
}

#btnBack, #btnRedo, #timerIcon, #timer {
    width: 30px;
    height: 30px;
    -webkit-box-shadow: none;
            box-shadow: none;
    border:none;
    background-color: transparent;
    background-repeat: no-repeat;
    margin:25px 5px;
    font-size: 28px;

}

#timer {
    margin-bottom: 3px;
}

#timerIcon {
    margin-left: 100px;
}

#btnRedo {
    background-image: url(icons/reset-A.png);
}

#btnRedo:hover {
    background-image: url(icons/reset-B.png)
}


#btnBack {
    background-image: url(icons/back-A.png);
}

 #btnBack:hover {
     background-image: url(icons/back-B.png)
 }


button.start {
    border: 2px solid #FFF;
    font-size: 20px;
    margin-top: 30px;
    margin-left: -10px;
    border-radius: 20px;
}

button:hover, button.start:hover {
    color: #025afa;
    -webkit-transition: color .3s;
    -o-transition: color .3s;
    transition: color .3s;
}

button.start:hover {
    border: 2px solid #025afa;
}

img {
    margin: 10px;
    border-radius: 10px;
    -webkit-box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, .75);
            box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, .75);
    max-width: 100px;
    max-height: 100px;
}


img.visible {
    -webkit-box-shadow: none;
            box-shadow: none;
}

img.preview {
    width: auto;
    height: auto;
    -webkit-box-shadow: none;
            box-shadow: none;
}

.welcome {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    color: white;
    margin: 3% 30%;
    -ms-flex-align: center;
        align-items: center;
    background-color: rgba(0,0,0, .25);
    padding: 20px;
    border-radius: 20px;
    -webkit-box-shadow: 0 0 20px 0 rgba(255, 255, 255, .2);
            box-shadow: 0 0 20px 0 rgba(255, 255, 255, .2);
}

.welcome.p {
    margin: 20px;
}

.cards {

    display: -ms-flexbox;

    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 90%;
    margin: 0 auto ;
    padding: 30px;
}

.header {
    text-align: center;
    padding: 40px 5px 0 0;
    -webkit-box-shadow: inset 0 0 20px 0 rgba(255, 255, 255, .2);
            box-shadow: inset 0 0 20px 0 rgba(255, 255, 255, .2);
}

.stats {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    /*align-items: center;*/
}

.stats p {
    font-size: 30px;
    margin: 0;
    color: #025AFA;
    margin-left: 55px;
}

#timer {

    color: #FFF;
    font-weight: lighter;
    margin-left: 5px;
}





